.mainContainer{
    display: flex;
    flex-direction:column;
    width:100vw;
    height:100vh;
    align-items: center 
}

.title{
    margin-top: 5vh;
    color:#009182;
}


.formContainer{
    margin-top: 10vh;
    width:40vw;
    height:60vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.inputContainer{
    display: flex;
    flex-direction: column;
}

.inputLabel{
    align-self: flex-start;
    font-family: Montserrat, sans-serif;
    color:#009182;
}

.inputField{
    font-family: Montserrat, sans-serif;
    margin-top:10px;
    align-self: center;
    width:100%;
    font-size: 18pt;
    height:40px;
    padding-bottom:5px;
    border:none;
    background-color: #0c0c0c;
    border-bottom:#009182 solid 2px;
    color:white;
}

.inputField::placeholder, .inputArea::placeholder{
    font-family: Montserrat, sans-serif;
}

.inputArea{
    margin-top:10px;
    align-self: center;
    width:100%;
    height:40px;
    font-size: 18pt;
    resize: none;
    padding-bottom:5px;
    border:none;
    background-color: #0c0c0c;
    border-bottom:#009182 solid 2px;
    color:white;
    font-family: Montserrat, sans-serif;
}

.inputButton{
    align-self: center;
    padding:10px 20px;
    background-color: #009182;
    color:white;
    border-radius: 10px;
    cursor: pointer;
    transition:0.5s;
    border:none;
}

.inputButton:hover{
    background-color: #05463f;
    transition:0.5s
}